@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
.wrapper {
  padding: 100px 0 0 100px;
  min-height: 100vh;
  background: linear-gradient(0deg, rgb(243, 241, 218) 27%, rgb(176, 230, 221) 93%);
@media (max-width: 490px) {
  padding: 10px;
  
}
}
.container {
  position: relative;
  width: 250px;
  max-height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #288d8a;
  box-shadow: 25px 25px 75px rgba(0, 0, 0, 0.25),
  10px 10px 70px rgba(0, 0, 0, 0.25),
  inset 5px 5px 10px rgba(0, 0, 0, 0.5),
  inset 5px 5px 20px rgba(255, 255, 255, 0.2),
  inset -5px -5px 15px rgba(0, 0, 0, 0.75);
  border-radius: 20px;
  padding: 30px 20px;
  & h3 {
    color: #fff;
    font-weight: 600;
    font-size: 1.3em;
    width: 100%;
    text-align: center;
    margin: 0 0 20px 0;
    letter-spacing: 2px;
    text-transform: uppercase;
  }
  & form {
    position: relative;
    width: 100%;
  }
  @media (max-width: 490px) {
    max-width: 100%;
  border-radius: 7px;
    font-size: 0.6rem;
    & h3{
      font-size: 0.8rem;
    }
  }
}
.inputBox {
  position: relative;
  width: 100%;
  margin: 0 0 20px 0;
  & span {
    display: inline-block;
    color: #fff;
    margin: 0 0 10px 0;
    padding: 0 0 0 6px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 0.75em;
    border-left: 4px solid #fff;
  }
  @media (max-width: 490px) {
    margin: 0;
  }
  & .box {
    display: flex;
    & .icon {
      position: relative;
      min-width: 30px;
      height: 30px;
      background-color: #ff2c74;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      margin: 0 10px 0 0;
      color: #fff;
      font-size: 1.2em;
      box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.25),
      inset 2px 2px 5px rgba(255, 255, 255, 0.25),
      inset -3px -3px 5px rgba(0, 0, 0, 0.5);
      @media (max-width: 490px) {
        min-width: 20px;
      height: 20px;
      }
    }
    & input {
      position: relative;
      width: 100%;
      height: 30px;
      border: none;
      padding: 10px 20px;
      border-radius: 30px;
      box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.25),
      inset 2px 2px 5px rgba(0, 0, 0, 0.25),
      inset -3px -3px 5px rgba(0, 0, 0, 0.5);
      @media (max-width: 490px) {
        height: 20px;
        height: 20px;
      }
    }
  }
}
.box {
  & button {
    position: relative;
    width: 100%;
    border: none;
    outline: none;
    margin: 10px 0 0 0;
    padding: 10px 20px;
    border-radius: 30px;
    background-color: #275169;
    box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.25),
    inset 2px 2px 5px rgba(255, 255, 255, 0.25),
    inset -3px -3px 5px rgba(0, 0, 0, 0.5);
    color: #fff;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 600;
    &:hover {
      filter: brightness(1.1);
    }
  }
}
.checkbox {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.85em;
  display: flex;
  align-items: center;
  & input {
    margin: 0 0 0 5px;
  }
  @media (max-width: 490px) {
  font-size: 0.6rem;
  }
}
.loginError {
  width: 100%;
  height: 40px;
  border-radius: 5px;
  color: #e73d3a;
  border: 2px solid #e73d3a;
  background-color: #af9896;
  margin: 0 0 10px 0;
  padding: 5px;
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.captcha{
  width: 100%;
  & img{
    width: 100%;
  }
  & input{
   width: 100%;
  }
}